import wx from 'weixin-js-sdk'
import router from '../../router'
export var wexinPay = function (data) {
  wx.config({
    debug: false,
    appId: data.appid,
    timestamp: data.timeStamp, // timestamp,
    nonceStr: data.nonce_str,
    signature: data.paySign,
    jsApiList: ['chooseWXPay']
  })
  wx.ready(function () {
    wx.checkJsApi({
      jsApiList: ['chooseWXPay'], // JSJS2,
      success: function (res) {
        // apitruefalse
        // {"checkResult":{"chooseImage":true},"errMsg":"checkJsApi:ok"}
      }
    })
    wx.chooseWXPay({
      timestamp: data.timeStamp, // timestamp,
      nonceStr: data.nonce_str,
      package: 'prepay_id=' + data.prepay_id, // prepay_idprepay_id=***
      signType: 'MD5',
      paySign: data.paySign,
      success: function (res) {
        localStorage.removeItem('PAYTYPE')
        router.replace({
          path: '/pay/paySuccess'
        })
      }
    })
  })
}
