
const emptyTips = '--'            // 
const dateFns = require('date-fns')
let money = (v, isThousand, isW) => {                                                     // 
  if (!v && v !== 0) return 0
  if (isW) {
    return parseFloat(v) >= 10000 ? (v / 10000).toFixed(1) + '' : (v * 100 / 100).toFixed(2).replace(/(\d)(?=(\d{3})+\.)/g, '$1,')
  } if (isThousand) { // )
    return (v * 100 / 100).toFixed(2).replace(/(\d)(?=(\d{3})+\.)/g, '$1,')
  } else { // 
    return (v * 100 / 100).toFixed(2)
  }
}
let thousandBit = (v) => {                                                          // 
  if ((!v && v !== 0) || v === 0) return 0
  return v.toString().replace(/\d{1,3}(?=(\d{3})+(\.|$))/g, '$&,')
}
let filterPhone = (val) => {                                                       // 
  if (!val) return emptyTips
  return val.toString().replace(/(.{3}).*(.{3})/, '$1*****$2')
}
let orderStatus = (v) => {                                                          //   1 2  3  4  5 6 7 
  let statusArr = ['', '', '', '', '', '', '']
  return statusArr[v - 1]
}
let filterCard = (val) => {                                                          // 
  if (!val) return emptyTips
  return val.toString().replace(/(.{2}).*(.{2})/, '$1************$2')
}

let sexStatusFilter = (v) => {                                                       // 
  let statusArr = ['', '']
  return statusArr[Number(v - 1)]
}
let idcardIsPassFilter = (v) => {                                                    // 
  let statusArr = ['', '']
  return statusArr[v]
}
let filterName = (val) => {                                                          // 
  if (!val) return emptyTips
  var name = ''
  for (var i = 0; i < val.length - 1; i++) {
    name += '*'
  }
  return val.replace(/(.{1}).*(.{0})/, '$1' + name)
}

let cardNoFilter = (val) => {                                                         // 
  if (!val) return emptyTips
  return val.replace(/(\d{4})\d{1,}(\d{4})/, '$1  ****  ****  $2')
}

let cardTypeFilter = (v) => {                                                         // 
  // if (!v) return ''
  let statusArr = ['', '']
  return statusArr[v - 1]
}
let filterEmpty = (value, unit = '') => {                                             // 
  if (!value) return emptyTips
  return value + unit
}
let filterDateTime = (date) => {                                                       // 
  var tt = new Date(parseInt(date))
  var days = parseInt((new Date().getTime() - date) / 86400000)
  var today = new Date().getDate()
  // var year = tt.getFullYear()
  // var mouth = tt.getMonth() + 1
  var day = tt.getDate()
  var time = tt.getHours() < 10 ? '0' + tt.getHours() : tt.getHours()
  var min = tt.getMinutes() < 10 ? '0' + tt.getMinutes() : tt.getMinutes()
  var result
  var offset
  offset = Math.abs(today - day)
  if (days < 2 && offset < 2) {
    if (offset === 0) {
      result = '' + time + ':' + min
    } else if (offset === 1) {
      result = '' + time + ':' + min
    }
    return result
  } else {
    return filterDate(date, 'YYYY-MM-DD')
  }
}
let filterDate = (value, type) => {
  if (!value) return emptyTips
  let filterType = type || 'YYYY-MM-DD HH:mm:ss'
  return dateFns.format(parseInt(value), filterType)
}
let integralSourceFilter = (val) => {                                                     // 
  let statusArr = [
    {
      code: 101,
      val: ''
    },
    {
      code: 102,
      val: ''
    },
    {
      code: 103,
      val: ''
    },
    {
      code: 104,
      val: ''
    },
    {
      code: 105,
      val: ''
    },
    {
      code: 106,
      val: ''
    },
    {
      code: 107,
      val: ''
    },
    {
      code: 108,
      val: ''
    },
    {
      code: 109,
      val: ''
    },
    {
      code: 15,
      val: ''
    },
    {
      code: 41,
      val: ''
    }
  ]
  var res = statusArr.filter(row => row.code === val)
  return res[0] ? res[0].val : '--'
}
export {
  money,
  filterPhone,
  orderStatus,
  filterCard,
  thousandBit,
  sexStatusFilter,
  idcardIsPassFilter,
  filterName,
  cardNoFilter,
  cardTypeFilter,
  filterEmpty,
  filterDateTime,
  filterDate,
  integralSourceFilter
}
